package interactor.util

import interactor.manager.StateManager.getState
import interactor.manager.StateManager.updateState
import presentation.Screens
import presentation.Screens.*

fun goToLogin() {
    updateState(getState().copy(mainView = Login))
}

fun goToRegister() {
    updateState(getState().copy(mainView = Register))
}

fun goToNavigation() {
    updateState(getState().copy(mainView = Navigation))
}

fun goToAddBusiness() {
    updateState(getState().copy(mainView = AddBusiness))
}

fun pushView(screen: Screens) {
    val navStack = getState().navViewStack
    navStack.push(screen)
    updateState(getState().copy(navView = navStack.peek(), navViewStack = navStack))
}

fun popView() {
    val navStack = getState().navViewStack
    navStack.pop()
    updateState(getState().copy(navView = navStack.peek(), navViewStack = navStack))
}