package presentation.splash.manager

import datasource.protobuf.checkProtoBuf
import interactor.manager.StateManager.launchDefault
import interactor.util.goToLogin
import kotlinx.coroutines.delay
import presentation.splash.event.SplashUiEvents

object SplashManager {
    fun onTriggerEvent(event: SplashUiEvents) {
        when (event) {
            SplashUiEvents.GetSplashUiData -> {
                launchDefault {
                    delay(3000)
                    checkProtoBuf()
                    goToLogin()
                }
            }
        }
    }
}