package datasource.network.client

import datasource.network.response.GSTDetails
import datasource.network.response.PincodeAddress
import datasource.protobuf.CategoriesJson
import datasource.protobuf.CategoriesProto
import datasource.protobuf.PinCoordinatesJson
import datasource.protobuf.PinCoordinatesProto
import datasource.protobuf.PincodeCProto
import io.ktor.client.HttpClient
import io.ktor.client.call.body
import io.ktor.client.plugins.contentnegotiation.ContentNegotiation
import io.ktor.client.plugins.logging.DEFAULT
import io.ktor.client.plugins.logging.LogLevel
import io.ktor.client.plugins.logging.Logger
import io.ktor.client.plugins.logging.Logging
import io.ktor.client.request.HttpRequestBuilder
import io.ktor.client.request.delete
import io.ktor.client.request.get
import io.ktor.client.request.header
import io.ktor.client.request.patch
import io.ktor.client.request.post
import io.ktor.client.request.put
import io.ktor.client.request.setBody
import io.ktor.client.statement.HttpResponse
import io.ktor.client.statement.bodyAsText
import io.ktor.http.ContentType
import io.ktor.http.HttpHeaders
import io.ktor.http.HttpMethod
import io.ktor.http.HttpStatusCode
import io.ktor.serialization.kotlinx.json.json
import kotlinx.serialization.Serializable
import kotlinx.serialization.SerializationException
import kotlinx.serialization.json.Json
import util.BuildConfig
import util.PlatformType

val PROXY = "https://cors-anywhere.herokuapp.com/"
val gstDetails = "https://cb-lite-back.captain.biz/invoice-master/api/v1/invoice/get_gst_details?gst_number="
val pincodeAddress = "https://khatory-4fc9b-default-rtdb.firebaseio.com/Pincode/"
val categoriesJson = "https://khatory-4fc9b-default-rtdb.firebaseio.com/Categories"
val categoriesProtoZipBase64 = "https://khatory-4fc9b-default-rtdb.firebaseio.com/CategoriesProtoZipBase64"
val categoriesProto = "https://firebasestorage.googleapis.com/v0/b/khatory-4fc9b.appspot.com/o/test%2FCategoriesProto.bin"
val categoriesProtoComp = "https://firebasestorage.googleapis.com/v0/b/khatory-4fc9b.appspot.com/o/test%2FCategoriesProtoComp.bin"
val categoriesText = "https://firebasestorage.googleapis.com/v0/b/khatory-4fc9b.appspot.com/o/test%2FCategoriesText.bin"
val pincodeProto = "https://authtest-a5ec4.firebaseio.com/PincodesProto/"
val pincodeJson = "https://authtest-a5ec4.firebaseio.com/PincodesJson/"
val pincodeC = "https://authtest-a5ec4.firebaseio.com/PincodesC/"
val jsonString = ".json"

fun getGstDetailsUrl(gstNumber: String): String {
    if (BuildConfig().platform() == PlatformType.WEB) {
        return "$PROXY$gstDetails$gstNumber"
    }
    return "$gstDetails$gstNumber"
}

fun getPincodeAddressUrl(pincode: String): String {
    return "$pincodeAddress$pincode$jsonString"
}

fun getCategoriesJsonUrl(): String {
    return "$categoriesJson$jsonString"
}

fun getCategoriesProtoZipBase64Url(): String {
    return "$categoriesProtoZipBase64$jsonString"
}

fun getPincodeJson(pincode: String): String {
    return "$pincodeJson$pincode$jsonString"
}

fun getPincodeProto(pincode: String): String {
    return "$pincodeProto$pincode$jsonString"
}

fun getPincodeCProto(): String {
    return "$pincodeC$jsonString"
}

object KtorClient {
    val client: HttpClient = HttpClient {
        install(ContentNegotiation) {
            json(Json { isLenient = true; ignoreUnknownKeys = true })
        }
        install(Logging) {
            logger = Logger.DEFAULT
            level = LogLevel.ALL
        }
    }

    @Serializable
    data class ErrorResponse(val errorCode: Int, val message: String)

    suspend inline fun <reified T> makeApiRequest(
        url: String,
        httpMethod: HttpMethod,
        headers: Map<String, String> = emptyMap(),
        requestBody: Any? = null
    ): Result<T> {
        return try {
            val response: HttpResponse = when (httpMethod) {
                HttpMethod.Get -> client.get(url) {
                    applyHeaders(headers)
                }
                HttpMethod.Post -> client.post(url) {
                    applyHeaders(headers)
                    requestBody?.let { setBody(it) }
                }
                HttpMethod.Put -> client.put(url) {
                    applyHeaders(headers)
                    requestBody?.let { setBody(it) }
                }
                HttpMethod.Delete -> client.delete(url) {
                    applyHeaders(headers)
                    requestBody?.let { setBody(it) }
                }
                HttpMethod.Patch -> client.patch(url) {
                    applyHeaders(headers)
                    requestBody?.let { setBody(it) }
                }
                else -> throw IllegalArgumentException("Unsupported HTTP method")
            }

            when (response.status) {
                HttpStatusCode.OK, HttpStatusCode.Created, HttpStatusCode.Accepted -> {
                    val responseBody: T = response.body()
                    Result.success(responseBody)
                }
                else -> {
                    val errorBody = response.bodyAsText()
                    val errorResponse: ErrorResponse? = try {
                        Json.decodeFromString<ErrorResponse>(errorBody)
                    } catch (e: SerializationException) {
                        null
                    }
                    Result.failure(Exception("Error: ${errorResponse?.message ?: errorBody}"))
                }
            }
        } catch (e: Exception) {
            Result.failure(e)
        }
    }

    fun HttpRequestBuilder.applyHeaders(headers: Map<String, String>) {
        headers.forEach { (key, value) ->
            header(key, value)
        }
    }

    suspend fun getGSTDetails(gstNumber: String): Result<GSTDetails> {
        // val headers = mapOf("Authorization" to "Bearer your_token_here")
        return makeApiRequest(getGstDetailsUrl(gstNumber), HttpMethod.Get)
    }

    suspend fun getPincodeAddress(pincode: String): Result<PincodeAddress> {
        return makeApiRequest(getPincodeAddressUrl(pincode), HttpMethod.Get)
    }

    suspend fun getCategoriesJson(): Result<CategoriesJson> {
        return makeApiRequest(getCategoriesJsonUrl(), HttpMethod.Get)
    }

    suspend fun getCategoriesProtoZipBase64(): Result<CategoriesProto> {
        return makeApiRequest(getCategoriesProtoZipBase64Url(), HttpMethod.Get)
    }

    suspend fun uploadCategoriesProto(data: ByteArray): Result<Unit> {
        return makeApiRequest(categoriesProto, HttpMethod.Post, mapOf(HttpHeaders.ContentType to "application/octet-stream"), data)
    }

    suspend fun getCategoriesProto(): Result<ByteArray> {
        return makeApiRequest("$categoriesProto?alt=media", HttpMethod.Get)
    }

    suspend fun uploadCategoriesProtoComp(data: ByteArray): Result<Unit> {
        return makeApiRequest(categoriesProtoComp, HttpMethod.Post, mapOf(HttpHeaders.ContentType to "application/octet-stream"), data)
    }

    suspend fun getCategoriesProtoComp(): Result<ByteArray> {
        return makeApiRequest("$categoriesProtoComp?alt=media", HttpMethod.Get)
    }

    suspend fun uploadCategoriesText(data: ByteArray): Result<Unit> {
        return makeApiRequest(categoriesText, HttpMethod.Post, mapOf(HttpHeaders.ContentType to "application/octet-stream"), data)
    }

    suspend fun getCategoriesText(): Result<ByteArray> {
        return makeApiRequest("$categoriesText?alt=media", HttpMethod.Get)
    }

    suspend fun updatePincodeJson(pincode: String, pinCoordinatesJson: PinCoordinatesJson): Result<Unit> {
        return makeApiRequest(getPincodeJson(pincode), HttpMethod.Put, mapOf(HttpHeaders.ContentType to "application/json"), pinCoordinatesJson)
    }

    suspend fun updatePincodeProto(pincode: String, pinCoordinatesProto: PinCoordinatesProto): Result<Unit> {
        return makeApiRequest(getPincodeProto(pincode), HttpMethod.Put, mapOf(HttpHeaders.ContentType to "application/json"), pinCoordinatesProto)
    }

    suspend fun updatePincodeC(pincodeCProto: PincodeCProto): Result<Unit> {
        return makeApiRequest(getPincodeCProto(), HttpMethod.Put, mapOf(HttpHeaders.ContentType to "application/json"), pincodeCProto)
    }
}