package presentation.navigation.manager

import interactor.util.popView
import interactor.util.pushView
import presentation.navigation.event.NavigationUiEvents

object NavigationManager {
    fun onTriggerEvent(event: NavigationUiEvents) {
        when(event) {
            NavigationUiEvents.PopView -> popView()
            is NavigationUiEvents.PushView -> pushView(event.screen)
        }
    }
}