package interactor.util

import interactor.manager.StateManager.getState
import interactor.manager.StateManager.updateState
import presentation.dialog.uistate.DialogUiState

fun showLoading() {
    updateState(getState().copy(loadingUiState = DialogUiState()))
}

fun hideLoading() {
    updateState(getState().copy(loadingUiState = null))
}