package presentation.common.menu

import androidx.compose.foundation.background
import androidx.compose.foundation.layout.fillMaxWidth
import androidx.compose.material.DropdownMenuItem
import androidx.compose.material.ExperimentalMaterialApi
import androidx.compose.material.ExposedDropdownMenuBox
import androidx.compose.material.ExposedDropdownMenuDefaults
import androidx.compose.material.OutlinedTextField
import androidx.compose.material.TextFieldColors
import androidx.compose.material.TextFieldDefaults
import androidx.compose.runtime.Composable
import androidx.compose.runtime.getValue
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.remember
import androidx.compose.runtime.setValue
import androidx.compose.ui.Modifier
import androidx.compose.ui.graphics.Color.Companion.Black
import androidx.compose.ui.text.TextStyle
import androidx.compose.ui.text.font.FontWeight
import androidx.compose.ui.unit.sp
import presentation.common.text.Text14Regular
import theme.InterMedium
import theme.focussed
import theme.message
import theme.unfocussed
import theme.white

@OptIn(ExperimentalMaterialApi::class)
@Composable
fun OutlinedExpandedMenu(
    label: String = "",
    selectedIndex: Int,
    options: List<String>,
    onSelected: (Int) -> Unit,
) {
    var expanded by remember { mutableStateOf(false) }
    ExposedDropdownMenuBox(
        modifier = Modifier.fillMaxWidth(),
        expanded = expanded,
        onExpandedChange = { expanded = !expanded }) {
        OutlinedTextField(
            modifier = Modifier.fillMaxWidth(),
            readOnly = true,
            value = if (selectedIndex >= 0 && selectedIndex < options.size) options[selectedIndex] else "",
            onValueChange = { },
            label = { Text14Regular(text = label, color = message) },
            textStyle = TextStyle(
                fontFamily = InterMedium(),
                fontSize = 14.sp,
                fontWeight = FontWeight(500)
            ),
            trailingIcon = { ExposedDropdownMenuDefaults.TrailingIcon(expanded = expanded) },
            colors = getMenuOutlineColor(),
        )
        ExposedDropdownMenu(modifier = Modifier
            .fillMaxWidth()
            .background(white),
            expanded = expanded,
            onDismissRequest = { expanded = false }) {
            options.forEachIndexed { index: Int, selectionOption: String ->
                DropdownMenuItem(modifier = Modifier.fillMaxWidth(),
                    onClick = {
                        expanded = false
                        onSelected(index)
                    }
                ) { Text14Regular(text = selectionOption, color = Black) }
            }
        }
    }
}

@Composable
fun getMenuOutlineColor(): TextFieldColors {
    return TextFieldDefaults.outlinedTextFieldColors(
        backgroundColor = white,
        textColor = Black,
        cursorColor = Black,
        focusedBorderColor = focussed,
        unfocusedBorderColor = focussed,
        unfocusedLabelColor = unfocussed,
        focusedLabelColor = message
    )
}