package presentation.business.add.manager

import datasource.network.client.KtorClient
import interactor.manager.StateManager.launchDefault
import interactor.util.goToNavigation
import interactor.util.hideLoading
import interactor.util.showLoading
import presentation.business.add.event.AddBusinessUiEvents
import presentation.business.add.uistate.getAddBusinessState
import util.isValidGst
import util.isValidZipcode

object AddBusinessManager {
    fun onTriggerEvent(event: AddBusinessUiEvents) {
        when(event) {
            is AddBusinessUiEvents.GstTypeIndex -> getAddBusinessState().gstTypeIndex(event)
            is AddBusinessUiEvents.Gst -> {
                getAddBusinessState().gst(event)
                if (isValidGst(event.gst)) {
                    showLoading()
                    launchDefault {
                        val result = KtorClient.getGSTDetails(event.gst)
                        if (result.isSuccess) {
                            getAddBusinessState().gstDetails(result.getOrNull())
                        }
                        hideLoading()
                    }
                }
            }
            is AddBusinessUiEvents.AadharOrPAN -> getAddBusinessState().aadharOrPAN(event)
            is AddBusinessUiEvents.BusinessName -> getAddBusinessState().businessName(event)
            is AddBusinessUiEvents.BusinessTypeIndex -> getAddBusinessState().businessTypeIndex(event)
            is AddBusinessUiEvents.IndustryTypeIndex -> getAddBusinessState().industryTypeIndex(event)
            is AddBusinessUiEvents.Zipcode -> {
                getAddBusinessState().zipcode(event)
                if (isValidZipcode(event.zipcode)) {
                    showLoading()
                    launchDefault {
                        val result = KtorClient.getPincodeAddress(event.zipcode)
                        if (result.isSuccess) {
                            getAddBusinessState().getPincodeAddress(result.getOrNull())
                        }
                        hideLoading()
                    }
                }
            }
            is AddBusinessUiEvents.Address -> getAddBusinessState().address(event)
            is AddBusinessUiEvents.City -> getAddBusinessState().city(event)
            is AddBusinessUiEvents.StateIndex -> getAddBusinessState().stateIndex(event)
            AddBusinessUiEvents.AddBusiness -> goToNavigation()
        }
    }
}