package presentation.register.view

import androidx.compose.foundation.background
import androidx.compose.foundation.clickable
import androidx.compose.foundation.layout.Spacer
import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.foundation.layout.height
import androidx.compose.foundation.layout.padding
import androidx.compose.foundation.lazy.LazyColumn
import interactor.event.UiEvents
import interactor.uistate.UiState
import androidx.compose.runtime.Composable
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.unit.dp
import presentation.common.button.IMSButton
import presentation.common.edittext.EmailEditText
import presentation.common.edittext.FirstNameEditText
import presentation.common.edittext.LastNameEditText
import presentation.common.edittext.MobileNumberEditText
import presentation.common.edittext.PasswordEditText
import presentation.common.image.SplashImage
import presentation.common.text.Text14SemiBold
import presentation.register.event.RegisterUiEvents
import theme.primary
import theme.white

@Composable
fun RegisterView(uiState: UiState, onTriggerEvent: (UiEvents) -> Unit) {
    LazyColumn(
        modifier = Modifier.fillMaxSize().background(color = white).padding(horizontal = 16.dp),
        horizontalAlignment = Alignment.CenterHorizontally
    ) {
        item {
            Spacer(Modifier.height(30.dp))
            SplashImage()
            Spacer(Modifier.height(30.dp))
            FirstNameEditText(
                text = uiState.registerState.firstName,
                hint = uiState.registerState.firstNameLabel,
            ) { onTriggerEvent(UiEvents.RegisterEvents(RegisterUiEvents.FirstName(it))) }
            Spacer(Modifier.height(16.dp))
            LastNameEditText(
                text = uiState.registerState.lastName,
                hint = uiState.registerState.lastNameLabel,
            ) { onTriggerEvent(UiEvents.RegisterEvents(RegisterUiEvents.LastName(it))) }
            Spacer(Modifier.height(16.dp))
            MobileNumberEditText(
                text = uiState.registerState.mobileNumber,
                hint = uiState.registerState.mobileNumberLabel,
            ) { onTriggerEvent(UiEvents.RegisterEvents(RegisterUiEvents.MobileNumber(it))) }
            Spacer(Modifier.height(16.dp))
            EmailEditText(
                text = uiState.registerState.email,
                hint = uiState.registerState.emailLabel,
            ) { onTriggerEvent(UiEvents.RegisterEvents(RegisterUiEvents.Email(it))) }
            Spacer(Modifier.height(16.dp))
            PasswordEditText(
                text = uiState.registerState.password,
                hint = uiState.registerState.passwordLabel,
            ) { onTriggerEvent(UiEvents.RegisterEvents(RegisterUiEvents.Password(it))) }
            Spacer(Modifier.height(25.dp))
            IMSButton(
                text = uiState.registerState.registerLabel,
                onClick = { onTriggerEvent(UiEvents.RegisterEvents(RegisterUiEvents.Register)) }
            )
            Spacer(Modifier.height(25.dp))
            Text14SemiBold(uiState.registerState.goToLoginLabel, primary, Modifier.clickable {
                onTriggerEvent(UiEvents.RegisterEvents(RegisterUiEvents.GoToLogin))
            })
            Spacer(Modifier.height(30.dp))
        }
    }
}
