package interactor.event

import presentation.business.add.event.AddBusinessUiEvents
import presentation.login.event.LoginUiEvents
import presentation.navigation.event.NavigationUiEvents
import presentation.register.event.RegisterUiEvents
import presentation.splash.event.SplashUiEvents

sealed class UiEvents {
    data class Portrait(val isPortrait: Boolean) : UiEvents()
    data class SplashEvents(val splashUiEvents: SplashUiEvents) : UiEvents()
    data class LoginEvents(val loginEvents: LoginUiEvents) : UiEvents()
    data class RegisterEvents(val registerEvents: RegisterUiEvents) : UiEvents()
    data class NavigationEvents(val navigationEvents: NavigationUiEvents) : UiEvents()
    data class AddBusinessEvents(val addBusinessUiEvents: AddBusinessUiEvents) : UiEvents()
}