package presentation.register.manager

import interactor.util.goToAddBusiness
import interactor.util.goToLogin
import presentation.register.event.RegisterUiEvents
import presentation.register.uistate.getRegisterState

object RegisterManager {
    fun onTriggerEvent(event: RegisterUiEvents) {
        when (event) {
            is RegisterUiEvents.FirstName -> getRegisterState().setFirstName(event)
            is RegisterUiEvents.LastName -> getRegisterState().setLastName(event)
            is RegisterUiEvents.MobileNumber -> getRegisterState().setMobileNumber(event)
            is RegisterUiEvents.Email -> getRegisterState().setEmail(event)
            is RegisterUiEvents.Password -> getRegisterState().setPassword(event)
            RegisterUiEvents.Register -> goToAddBusiness()
            RegisterUiEvents.GoToLogin -> goToLogin()
        }
    }
}