package presentation.navigation.view

import androidx.compose.foundation.layout.RowScope
import androidx.compose.material.BottomNavigationItem
import androidx.compose.runtime.Composable
import androidx.compose.runtime.getValue
import androidx.navigation.NavHostController
import androidx.navigation.compose.currentBackStackEntryAsState
import org.jetbrains.compose.resources.DrawableResource
import presentation.Screens
import presentation.Screens.Purchase
import presentation.Screens.Sale
import presentation.Screens.Party
import presentation.Screens.Product
import presentation.common.icon.BottomBarIcon
import presentation.common.text.Text11Regular
import theme.*

@Composable
fun RowScope.NavigationItem(screen: Screens, navController: NavHostController) {
    val navBackStackEntry by navController.currentBackStackEntryAsState()
    val currentRoute = navBackStackEntry?.destination?.route
    BottomNavigationItem(selectedContentColor = white,
        unselectedContentColor = unselected,
        icon = { BottomBarIcon(res = getNavIcon(screen)) },
        label = { Text11Regular(screen.toString(), if (currentRoute == screen.toString()) white else unselected) },
        selected = currentRoute == screen.toString(),
        onClick = {
            navController.navigate(screen.toString()) {
                navController.graph.startDestinationRoute?.let { route ->
                    popUpTo(route) {
                        saveState = true
                    }
                }
                launchSingleTop = true
                restoreState = true
            }
        }
    )
}

fun getNavIcon(screen: Screens): DrawableResource {
    return when(screen) {
        Purchase -> buy
        Sale -> sell
        Party -> party
        Product -> product
        else -> { home }
    }
}
