package presentation.business.add.view

import androidx.compose.foundation.background
import androidx.compose.foundation.layout.Spacer
import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.foundation.layout.height
import androidx.compose.foundation.layout.padding
import androidx.compose.foundation.lazy.LazyColumn
import androidx.compose.runtime.Composable
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.graphics.Color.Companion.Black
import androidx.compose.ui.unit.dp
import interactor.event.UiEvents
import interactor.uistate.UiState
import presentation.business.add.event.AddBusinessUiEvents
import presentation.common.button.IMSButton
import presentation.common.edittext.*
import presentation.common.menu.OutlinedExpandedMenu
import presentation.common.text.Text24SemiBold
import presentation.login.event.LoginUiEvents
import theme.white

@Composable
fun AddBusinessView(uiState: UiState, onTriggerEvent: (UiEvents) -> Unit) {
    LazyColumn(
        modifier = Modifier.fillMaxSize().background(color = white).padding(horizontal = 16.dp),
        horizontalAlignment = Alignment.CenterHorizontally
    ) {
        item {
            Spacer(Modifier.height(20.dp))
            Text24SemiBold(
                text = uiState.addBusinessState.title,
                color = Black
            )
            Spacer(Modifier.height(8.dp))
            OutlinedExpandedMenu(
                label = uiState.addBusinessState.gstTypeLabel,
                selectedIndex = uiState.addBusinessState.gstType.indexOf(uiState.addBusinessState.addBusiness.gstType),
                options = uiState.addBusinessState.gstType,
            ) {
                onTriggerEvent(UiEvents.AddBusinessEvents(AddBusinessUiEvents.GstTypeIndex(it)))
            }
            Spacer(Modifier.height(8.dp))
            if (uiState.addBusinessState.isGstRegistered) {
                GstEditText(
                    hint = uiState.addBusinessState.gstLabel,
                    text = uiState.addBusinessState.addBusiness.gst,
                ) {
                    onTriggerEvent(UiEvents.AddBusinessEvents(AddBusinessUiEvents.Gst(it)))
                }
                Spacer(Modifier.height(8.dp))
            } else {
                AdhaarPanEditText(
                    hint = uiState.addBusinessState.adhaarPanLabel,
                    text = uiState.addBusinessState.addBusiness.aadharOrPAN,
                ) {
                    onTriggerEvent(UiEvents.AddBusinessEvents(AddBusinessUiEvents.AadharOrPAN(it)))
                }
                Spacer(Modifier.height(8.dp))
            }
            BusinessNameEditText(
                hint = uiState.addBusinessState.businessNameLabel,
                text = uiState.addBusinessState.addBusiness.businessName
            ) {
                onTriggerEvent(UiEvents.AddBusinessEvents(AddBusinessUiEvents.BusinessName(it)))
            }
            Spacer(Modifier.height(8.dp))
            OutlinedExpandedMenu(
                label = uiState.addBusinessState.businessTypeLabel,
                selectedIndex = uiState.addBusinessState.businessType.indexOf(uiState.addBusinessState.addBusiness.businessType),
                options = uiState.addBusinessState.businessType,
            ) {
                onTriggerEvent(UiEvents.AddBusinessEvents(AddBusinessUiEvents.BusinessTypeIndex(it)))
            }
            Spacer(Modifier.height(8.dp))
            OutlinedExpandedMenu(
                label = uiState.addBusinessState.industryTypeLabel,
                selectedIndex = uiState.addBusinessState.industryType.indexOf(uiState.addBusinessState.addBusiness.industryType),
                options = uiState.addBusinessState.industryType,
            ) {
                onTriggerEvent(UiEvents.AddBusinessEvents(AddBusinessUiEvents.IndustryTypeIndex(it)))
            }
            Spacer(Modifier.height(8.dp))
            ZipcodeEditText(
                hint = uiState.addBusinessState.zipcodeLabel,
                text = uiState.addBusinessState.addBusiness.zipcode
            ) {
                onTriggerEvent(UiEvents.AddBusinessEvents(AddBusinessUiEvents.Zipcode(it)))
            }
            Spacer(Modifier.height(8.dp))
            AddressEditText(
                hint = uiState.addBusinessState.addressLabel,
                text = uiState.addBusinessState.addBusiness.address
            ) {
                onTriggerEvent(UiEvents.AddBusinessEvents(AddBusinessUiEvents.Address(it)))
            }
            Spacer(Modifier.height(8.dp))
            CityEditText(
                hint = uiState.addBusinessState.cityLabel,
                text = uiState.addBusinessState.addBusiness.city
            ) {
                onTriggerEvent(UiEvents.AddBusinessEvents(AddBusinessUiEvents.City(it)))
            }
            Spacer(Modifier.height(8.dp))
            OutlinedExpandedMenu(
                label = uiState.addBusinessState.stateLabel,
                selectedIndex = uiState.addBusinessState.states.indexOf(uiState.addBusinessState.addBusiness.state),
                options = uiState.addBusinessState.states,
            ) {
                onTriggerEvent(UiEvents.AddBusinessEvents(AddBusinessUiEvents.StateIndex(it)))
            }
            Spacer(Modifier.height(25.dp))
            IMSButton(
                text = uiState.addBusinessState.title,
                onClick = { onTriggerEvent(UiEvents.AddBusinessEvents(AddBusinessUiEvents.AddBusiness)) }
            )
            Spacer(Modifier.height(30.dp))
        }
    }
}