package presentation.navigation.view

import androidx.compose.foundation.background
import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.Spacer
import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.foundation.layout.height
import androidx.compose.foundation.layout.width
import androidx.compose.runtime.Composable
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.unit.dp
import androidx.navigation.NavHostController
import interactor.event.UiEvents
import interactor.uistate.UiState
import org.jetbrains.compose.resources.painterResource
import presentation.common.expandablelist.ExpandableList
import presentation.common.image.CommonImage
import theme.splash
import theme.white

@Composable
fun NavigationDrawerView(
    uiState: UiState,
    onTriggerEvent: (UiEvents) -> Unit,
    navController: NavHostController
) {
    Column (
        modifier = Modifier.fillMaxSize().background(color = white),
        horizontalAlignment = Alignment.CenterHorizontally
    ) {
        Spacer(Modifier.height(15.dp))
        CommonImage(painterResource(splash), modifier = Modifier.width(200.dp).height(200.dp))
        Spacer(Modifier.height(15.dp))
        ExpandableList(uiState.navigationState.drawerSections)
        Spacer(Modifier.height(15.dp))
    }
}