package presentation.login.uistate

import interactor.manager.StateManager.getState
import interactor.manager.StateManager.updateState
import presentation.login.event.LoginUiEvents
import theme.*
import theme.getString

data class LoginState(
    val emailLabel: String = getString(emailString),
    val email: String = "",
    val passwordLabel: String = getString(passwordString),
    val password: String = "",
    val loginLabel: String = getString(loginString),
    val goToRegisterLabel: String = getString(dontHaveAnAccountRegisterString),
) {
   fun setEmail(event: LoginUiEvents.Email) {
       updateLoginState(this.copy(email = event.email))
   }

    fun setPassword(event: LoginUiEvents.Password) {
        updateLoginState(this.copy(password = event.password))
    }
}

fun updateLoginState(loginState: LoginState) {
    updateState(getState().copy(loginState = loginState))
}

fun getLoginState(): LoginState {
    return getState().loginState
}
