package presentation.register.event

sealed class RegisterUiEvents {
    data class FirstName(val firstName: String) : RegisterUiEvents()
    data class LastName(val lastName: String) : RegisterUiEvents()
    data class Email(val email: String) : RegisterUiEvents()
    data class MobileNumber(val mobileNumber: String) : RegisterUiEvents()
    data class Password(val password: String) : RegisterUiEvents()
    data object Register : RegisterUiEvents()
    data object GoToLogin : RegisterUiEvents()
}