package presentation.login.manager

import interactor.util.goToAddBusiness
import interactor.util.goToRegister
import presentation.login.event.LoginUiEvents
import presentation.login.uistate.getLoginState

object LoginManager {
    fun onTriggerEvent(event: LoginUiEvents) {
        when (event) {
            is LoginUiEvents.Email -> getLoginState().setEmail(event)
            is LoginUiEvents.Password -> getLoginState().setPassword(event)
            LoginUiEvents.Login -> goToAddBusiness()
            LoginUiEvents.GoToRegister -> goToRegister()
        }
    }
}