package presentation.business.add.uistate

import datasource.network.response.GSTDetails
import datasource.network.response.PincodeAddress
import domain.Business
import interactor.manager.StateManager.getState
import interactor.manager.StateManager.updateState
import presentation.business.add.event.AddBusinessUiEvents
import theme.*
import theme.getString
import util.businessTypeList
import util.gstTypeList
import util.industriesList
import util.statesList

data class AddBusinessState(
    val addBusiness: Business = Business(),
    val title: String = getString(addBusinessString),
    val isGstRegistered: Boolean = true,
    val gstTypeLabel: String = getString(gstRegistrationString),
    val gstType: List<String> = gstTypeList,
    val gstLabel: String = getString(enterGSTINString),
    val adhaarPanLabel: String = getString(enterAdhaarPanString),
    val businessNameLabel: String = getString(enterBusinessNameString),
    val businessTypeLabel: String = getString(businessTypeString),
    val businessType: List<String> = businessTypeList,
    val industryTypeLabel: String = getString(industryTypeString),
    val industryType: List<String> = industriesList,
    val zipcodeLabel: String = getString(enterZipString),
    val addressLabel: String = getString(enterAddressString),
    val cityLabel: String = getString(enterCityString),
    val stateLabel: String = getString(selectStateString),
    val states: List<String> = statesList,
) {
    fun gstTypeIndex(event: AddBusinessUiEvents.GstTypeIndex) {
        val gstType = getGstType()[event.gstTypeIndex]
        val isGstRegistered = gstType == getString(registeredUnderGSTNString)
        val gst = if (isGstRegistered) getAddBusiness().gst else ""
        val aadharOrPAN = if (isGstRegistered) "" else getAddBusiness().aadharOrPAN
        updateAddBusinessState(this.copy(addBusiness = getAddBusiness().copy(gstType = gstType, gst = gst, aadharOrPAN = aadharOrPAN), isGstRegistered = isGstRegistered))
    }

    fun gst(event: AddBusinessUiEvents.Gst) {
        updateAddBusinessState(this.copy(addBusiness = getAddBusiness().copy(gst = event.gst)))
    }

    fun aadharOrPAN(event: AddBusinessUiEvents.AadharOrPAN) {
        updateAddBusinessState(this.copy(addBusiness = getAddBusiness().copy(aadharOrPAN = event.aadharOrPAN)))
    }

    fun businessName(event: AddBusinessUiEvents.BusinessName) {
        updateAddBusinessState(this.copy(addBusiness = getAddBusiness().copy(businessName = event.businessName)))
    }

    fun businessTypeIndex(event: AddBusinessUiEvents.BusinessTypeIndex) {
        updateAddBusinessState(this.copy(addBusiness = getAddBusiness().copy(businessType = getBusinessType()[event.businessTypeIndex])))
    }

    fun industryTypeIndex(event: AddBusinessUiEvents.IndustryTypeIndex) {
        updateAddBusinessState(this.copy(addBusiness = getAddBusiness().copy(industryType = getIndustryType()[event.industryTypeIndex])))
    }

    fun zipcode(event: AddBusinessUiEvents.Zipcode) {
        updateAddBusinessState(this.copy(addBusiness = getAddBusiness().copy(zipcode = event.zipcode)))
    }

    fun address(event: AddBusinessUiEvents.Address) {
        updateAddBusinessState(this.copy(addBusiness = getAddBusiness().copy(address = event.address)))
    }

    fun city(event: AddBusinessUiEvents.City) {
        updateAddBusinessState(this.copy(addBusiness = getAddBusiness().copy(city = event.city)))
    }

    fun stateIndex(event: AddBusinessUiEvents.StateIndex) {
        updateAddBusinessState(this.copy(addBusiness = getAddBusiness().copy(state = getStates()[event.stateIndex])))
    }

    fun gstDetails(gstDetails: GSTDetails?) {
        updateAddBusinessState(this.copy(addBusiness = getAddBusiness().copy(
            businessName = gstDetails?.data?.details?.companyName ?: getAddBusiness().businessName,
            businessType = gstDetails?.data?.details?.companyType ?: getAddBusiness().businessType,
            zipcode = gstDetails?.data?.details?.pincode ?: getAddBusiness().zipcode,
            address = gstDetails?.data?.details?.companyAddress ?: getAddBusiness().address,
            state = gstDetails?.data?.details?.state?.name ?: getAddBusiness().state,
        )))
    }

    fun getPincodeAddress(pincodeAddress: PincodeAddress?) {
        updateAddBusinessState(this.copy(addBusiness = getAddBusiness().copy(
            address = pincodeAddress?.address + ", " + pincodeAddress?.district + ", " +  pincodeAddress?.state,
            city = pincodeAddress?.district ?: getAddBusiness().city,
            state = pincodeAddress?.state ?: getAddBusiness().state,
        )))
    }
}

fun updateAddBusinessState(addBusinessState: AddBusinessState) {
    updateState(getState().copy(addBusinessState = addBusinessState))
}

fun getAddBusinessState(): AddBusinessState {
    return getState().addBusinessState
}

fun getAddBusiness(): Business {
    return getState().addBusinessState.addBusiness
}

fun getGstType(): List<String> {
    return getState().addBusinessState.gstType
}

fun getBusinessType(): List<String> {
    return getState().addBusinessState.businessType
}

fun getIndustryType(): List<String> {
    return getState().addBusinessState.industryType
}

fun getStates(): List<String> {
    return getState().addBusinessState.states
}