package presentation.register.uistate

import interactor.manager.StateManager.getState
import interactor.manager.StateManager.updateState
import presentation.register.event.RegisterUiEvents
import theme.*

data class RegisterState(
    val firstNameLabel: String = getString(firstNameString),
    val firstName: String = "",
    val lastNameLabel: String = getString(lastNameString),
    val lastName: String = "",
    val mobileNumberLabel: String = getString(mobileNumberString),
    val mobileNumber: String = "",
    val emailLabel: String = getString(emailString),
    val email: String = "",
    val passwordLabel: String = getString(passwordString),
    val password: String = "",
    val registerLabel: String = getString(registerString),
    val goToLoginLabel: String = getString(alreadyHaveAnAccountLoginString),
) {
    fun setFirstName(event: RegisterUiEvents.FirstName) {
        updateRegisterState(this.copy(firstName = event.firstName))
    }

    fun setLastName(event: RegisterUiEvents.LastName) {
        updateRegisterState(this.copy(lastName = event.lastName))
    }

    fun setMobileNumber(event: RegisterUiEvents.MobileNumber) {
        updateRegisterState(this.copy(mobileNumber = event.mobileNumber))
    }

    fun setEmail(event: RegisterUiEvents.Email) {
        updateRegisterState(this.copy(email = event.email))
    }

    fun setPassword(event: RegisterUiEvents.Password) {
        updateRegisterState(this.copy(password = event.password))
    }
}

fun updateRegisterState(registerState: RegisterState) {
    updateState(getState().copy(registerState = registerState))
}

fun getRegisterState(): RegisterState {
    return getState().registerState
}