package interactor.view

import presentation.Screens.Login
import presentation.Screens.Navigation
import presentation.Screens.Register
import presentation.Screens.Splash
import presentation.Screens.AddBusiness
import androidx.compose.foundation.layout.Box
import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.foundation.layout.padding
import androidx.compose.material.Scaffold
import androidx.compose.runtime.Composable
import androidx.compose.ui.Modifier
import androidx.compose.ui.layout.onSizeChanged
import interactor.event.UiEvents
import interactor.uistate.UiState
import presentation.business.add.view.AddBusinessView
import presentation.common.animation.Animate
import presentation.dialog.view.DialogView
import presentation.login.view.LoginView
import presentation.navigation.view.NavigationView
import presentation.register.view.RegisterView
import presentation.splash.view.SplashView

@Composable
fun MainView(uiState: UiState, onTriggerEvent: (UiEvents) -> Unit) {
    Scaffold() { paddingValues ->
        Box(Modifier.fillMaxSize().padding(paddingValues).onSizeChanged {
            onTriggerEvent(UiEvents.Portrait(it.width < it.height))
        }) {
            when (uiState.mainView) {
                Splash -> Animate { SplashView(uiState, onTriggerEvent) }
                Login -> Animate { LoginView(uiState, onTriggerEvent) }
                Register -> Animate { RegisterView(uiState, onTriggerEvent) }
                Navigation -> Animate { NavigationView(uiState, onTriggerEvent) }
                AddBusiness -> Animate { AddBusinessView(uiState, onTriggerEvent) }
                else -> {}
            }
            uiState.loadingUiState?.let { DialogView(it) }
        }
    }
}
